import { Controller } from '@ali/4ever-cangjie';

export default function getListStyleType(controller: Controller) {
  const { value } = controller;
  const { selection, document } = value;
  const paragraph = selection && document
    // @ts-ignore
    .getClosest(selection.anchor.key, ({ type }) => type === 'paragraph');
  if (!paragraph) return '';
  const list = paragraph.data.list;
  return list?.listStyleType || '';
}
